export default {
	props: {
		value: {
			type: String,
			default:'',
		},
	},
	data() {
		return {
			inputVal: this.value,
		};
	},
	created() {
		
	},
	methods: {
		inputChange(event){
			var keyword = event.detail.value;
			this.$emit('input', keyword);
		},
	},
};
